import { Link, useMatch, useNavigate } from "react-router-dom";
import styled from "styled-components";
import { motion, useAnimation, useViewportScroll } from "framer-motion";
import { useState } from "react";
import { useEffect } from "react";
import { useForm } from "react-hook-form";

const Nav = styled(motion.nav)`
  width: 100%;
  position: fixed;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 55px;
  font-size: 14px;
`;

const NavCol = styled.div`
  display: flex;
  align-items: center;
`;

const Logo = styled(motion.p)`
  margin: 0 30px;
  width: 70px;
  font-size: 23px;
  font-weight: bold;
  color: ${(props) => props.theme.brandColor};
`;

const NavLinks = styled.ul`
  display: flex;
`;

const NavLinkItem = styled.li`
  margin-right: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: ${(props) => props.theme.textColor};
  a {
    display: flex;
    justify-content: center;
  }
`;

const Bar = styled(motion.span)`
  position: absolute;
  width: 45px;
  height: 5px;
  bottom: 0;
  border-radius: 5px;
  background-color: ${(props) => props.theme.brandColor};
`;

const Search = styled.form`
  display: flex;
  align-items: center;
  position: relative;
  svg {
    z-index: 10;
    width: 18px;
    margin-right: 30px;
    path {
      fill: ${(props) => props.theme.brandColor};
    }
  }
`;

const Input = styled(motion.input)`
  transform-origin: right center;
  position: absolute;
  left: -155px;
  border: 1px solid rgba(255, 255, 255, 0.8);
  padding: 5px;
  padding-left: 30px;
  background-color: ${(props) => props.theme.bgColor};
  color: ${(props) => props.theme.textColor};
  border-radius: 3px;
`;

const logoVariants = {
  normal: {
    fillOpacity: 1,
  },
  active: {
    fillOpacity: [0, 0.5, 0],
    transition: {
      repeat: Infinity,
    },
  },
};

const navVariants = {
  top: { backgroundColor: "rgba(0,0,0,0)" },
  scroll: { backgroundColor: "rgba(0,0,0,1)" },
};

interface IForm {
  keyword: string;
}

function Header() {
  const navigate = useNavigate();
  const { register, handleSubmit, setFocus } = useForm<IForm>();
  const onSearchValid = (data: IForm) => {
    navigate(`/search?keyword=${data.keyword}`);
  };
  const homeMatch = useMatch("/");
  const movieMatch = useMatch("/movie");
  const tvMatch = useMatch("/tv");
  const listMatch = useMatch("/bom");
  const [searchOpen, setSearchOpen] = useState(false);
  const { scrollY } = useViewportScroll();
  const navAnimaton = useAnimation();
  const toggleSearch = () => {
    setSearchOpen((prev) => !prev);
  };
  useEffect(() => {
    scrollY.onChange(() => {
      if (scrollY.get() > 50) {
        navAnimaton.start("scroll");
      } else {
        navAnimaton.start("top");
      }
    });
  }, [scrollY, navAnimaton]);
  return (
    <Nav variants={navVariants} initial="top" animate={navAnimaton}>
      <NavCol>
        <Link to="/">
          <Logo
          // variants={logoVariants}
          // initial="normal"
          // whileHover="active"
          // xmlns="http://www.w3.org/2000/svg"
          // width="1024"
          // height="276.742"
          // viewBox="0 0 1024 276.742"
          >
            DaBoda
          </Logo>
        </Link>
        <NavLinks>
          <NavLinkItem>
            <Link to="/">Home {homeMatch && <Bar layoutId="bar" />}</Link>
          </NavLinkItem>
          <NavLinkItem>
            <Link to="movie">Movie {movieMatch && <Bar layoutId="bar" />}</Link>
          </NavLinkItem>
          <NavLinkItem>
            <Link to="tv">TV Show {tvMatch && <Bar layoutId="bar" />}</Link>
          </NavLinkItem>
          <NavLinkItem>
            <Link to="bom">BomList {listMatch && <Bar layoutId="bar" />}</Link>
          </NavLinkItem>
        </NavLinks>
      </NavCol>
      <NavCol>
        <Search onSubmit={handleSubmit(onSearchValid)}>
          <motion.svg
            animate={{
              x: searchOpen ? -150 : 0,
            }}
            transition={{ type: "linear", duration: searchOpen ? 0.4 : 0.5 }}
            onClick={() => {
              toggleSearch();
              setFocus("keyword");
            }}
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 512 512"
          >
            <path d="M500.3 443.7l-119.7-119.7c27.22-40.41 40.65-90.9 33.46-144.7C401.8 87.79 326.8 13.32 235.2 1.723C99.01-15.51-15.51 99.01 1.724 235.2c11.6 91.64 86.08 166.7 177.6 178.9c53.8 7.189 104.3-6.236 144.7-33.46l119.7 119.7c15.62 15.62 40.95 15.62 56.57 0C515.9 484.7 515.9 459.3 500.3 443.7zM79.1 208c0-70.58 57.42-128 128-128s128 57.42 128 128c0 70.58-57.42 128-128 128S79.1 278.6 79.1 208z" />
          </motion.svg>
          <Input
            {...register("keyword", { required: true, minLength: 2 })}
            initial={{ scaleX: 0 }}
            animate={{ scaleX: searchOpen ? 1 : 0 }}
            transition={{ type: "linear", duration: searchOpen ? 0.4 : 0.4 }}
            placeholder="Search for contents"
          />
        </Search>
      </NavCol>
    </Nav>
  );
}

export default Header;
