export const SliderVariants = {
  stop: (isBack: boolean) => ({
    x: isBack ? -window.outerWidth - 5 : window.outerWidth + 5,
  }),
  moving: { x: 0 },
  next: (isBack: boolean) => ({
    x: isBack ? window.outerWidth + 5 : -window.outerWidth - 5,
  }),
};
export const SliderItemVariants = {
  normal: { scale: 1 },
  hover: {
    zIndex: 100,
    scale: 1.4,
    y: -50,
    transition: { delay: 0.3, type: "tween" },
  },
};
export const SliderBDTitleVariants = {
  hover: { y: -5, transition: { type: "tween", delay: 0.3 } },
};
export const InfoVariants = {
  hover: { opacity: 1, transition: { delay: 0.3, duration: 0.1 } },
};
export const BtnVariants = {
  normal: { fill: "rgb(255, 255, 255)" },
  hover: { fill: "rgb(100, 92, 255)" },
};
