const API_KEY = "e125ebbd22077e8bddd9f478a4019cfa";
const BASE_PATH = "https://api.themoviedb.org/3";

interface IMovieInfo {
  backdrop_path: string;
  id: number;
  original_language: string;
  original_title: string;
  overview: string;
  poster_path: string;
  release_date: string;
  title: string;
}

export interface IgetMoviesResult {
  dates: {
    maximum: string;
    minimum: string;
  };
  page: number;
  results: IMovieInfo[];
  total_pages: number;
  total_results: number;
}

interface IMovieSearchInfo {
  backdrop_path: string;
  id: number;
  overview: string;
  poster_path: string;
  release_date: string;
  title: string;
  video: boolean;
  vote_average: number;
  vote_count: number;
}

export interface IMovieSearch {
  page: number;
  results: IMovieSearchInfo[];
  total_results: number;
}

export function movieNowPlaying() {
  return fetch(
    `${BASE_PATH}/movie/now_playing?api_key=${API_KEY}&language=en-US&page=1&region=kr`
  ).then((response) => response.json());
}

export function movieSearch(keyword: any) {
  return fetch(
    `${BASE_PATH}/search/movie?api_key=${API_KEY}&language=en-US&query=${keyword}&page=1&include_adult=false&region=kr`
  ).then((response) => response.json());
}

interface ITvInfo {
  backdrop_path: string;
  first_air_date: string;
  id: number;
  name: string;
  origin_country: [string];
  original_language: string;
  original_name: string;
  overview: string;
  poster_path: string;
  vote_average: number;
  vote_count: number;
}

export interface ITvOntheair {
  page: number;
  results: ITvInfo[];
}

interface ITVSearchInfo {
  backdrop_path: string;
  first_air_date: string;
  id: number;
  name: string;
  origin_country: [string];
  original_language: string;
  original_name: string;
  overview: string;
  popularity: number;
  poster_path: string;
  vote_average: number;
  vote_count: number;
}

export interface ITVSearch {
  page: number;
  results: ITVSearchInfo[];
  total_results: number;
}

export function tvOntheair() {
  return fetch(
    `${BASE_PATH}/tv/on_the_air?api_key=${API_KEY}&language=en-US&page=1`
  ).then((response) => response.json());
}

export function tvSearch(keyword: any) {
  return fetch(
    `${BASE_PATH}/search/tv?api_key=${API_KEY}&language=en-US&page=1&query=${keyword}&include_adult=false`
  ).then((response) => response.json());
}
