import Helmet from "react-helmet";
import { useQuery } from "react-query";
import { useLocation } from "react-router-dom";
import styled from "styled-components";
import { Loader } from "../style/Elementary";
import { IMovieSearch, ITVSearch, movieSearch, tvSearch } from "../utility/api";
import { makeImagePath } from "../utility/utils";

const SearchWrapper = styled.div`
  margin-top: 5vh;
  padding: 30px;
`;

const SearchTitle = styled.h1`
  font-size: 30px;
  font-weight: bold;
  padding: 20px 0;
  margin-top: 30px;
`;

const SearchResult = styled.div`
  display: grid;
  width: 100%;
  grid-template-columns: repeat(6, 1fr);
  gap: 5px;
`;

const SearchBox = styled.div`
  background-position: center center;
  background-size: cover;
  height: 150px;
  display: flex;
  align-items: flex-end;
  p {
    padding: 14px;
    font-weight: bold;
  }
`;

const NoResult = styled.span`
  text-align: center;
  width: 100vw;
  margin: 0 auto;
  font-size: 20px;
`;

function Search() {
  const location = useLocation();
  const keyword = new URLSearchParams(location.search).get("keyword");
  const { data: movieData, isLoading: movieIsLoading } = useQuery<IMovieSearch>(
    [keyword, "searchMovie"],
    () => movieSearch(keyword)
  );
  const { data: tvData, isLoading: tvIsLoading } = useQuery<ITVSearch>(
    [keyword, "searchTV"],
    () => tvSearch(keyword)
  );
  return (
    <>
      <Helmet>
        <title>Daboda : Searching - {keyword} </title>
      </Helmet>
      {movieIsLoading || tvIsLoading ? (
        <Loader>Loading</Loader>
      ) : (
        <SearchWrapper>
          <SearchTitle>Search Results in Movie</SearchTitle>
          <SearchResult>
            {movieData?.total_results !== 0 ? (
              movieData?.results.map((i) =>
                i.backdrop_path !== null || "" ? (
                  <SearchBox
                    key={i.id}
                    style={{
                      backgroundImage: `url(${makeImagePath(i.backdrop_path)})`,
                    }}
                  >
                    <p>{i.title}</p>
                  </SearchBox>
                ) : null
              )
            ) : (
              <NoResult>No Result</NoResult>
            )}
          </SearchResult>

          <SearchTitle>Search Results in TV Shows</SearchTitle>
          <SearchResult>
            {tvData?.total_results !== 0 ? (
              tvData?.results.map((i) =>
                i.backdrop_path !== null || "" ? (
                  <SearchBox
                    key={i.id}
                    style={{
                      backgroundImage: `url(${makeImagePath(i.backdrop_path)})`,
                    }}
                  >
                    <p>{i.name}</p>
                  </SearchBox>
                ) : null
              )
            ) : (
              <NoResult>No Result</NoResult>
            )}
          </SearchResult>
        </SearchWrapper>
      )}
    </>
  );
}

export default Search;
